body {
  margin: 0;
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.NavBar{
  display: flex;
  flex-direction: row;
  height: 140px;
  width: 100%;
  align-items: center;
  background-color: black;
}
.Logo{
  height: 130px;
  width: 15%;
  margin-left: 1%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#logo{
  height: 95%;
  width: 65%;
}
.NavItems{
  display: flex;
  flex-direction: row;
  justify-content: right;
  align-items: center;
  height: 130px;
  width: 50%;
  margin-left: 33%;
  background-color: black;
}
.NavBtn{
  border: none;
  background-color: black;
  color: rgb(230, 140, 15);
  height: 90px;
  width: 20%;
  font-size: 22px;
}
.FormBody{
  display:flex;
  height: auto;
  width: 100%;
  background-color: black;
  justify-content: center;
  align-items: center;
}
.Form{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  width: 50%;
  background-color: #101010;
  border-radius: 10px;
  padding-top: 30px;
  padding-bottom: 30px;
}
.Heading{
  color: rgb(230, 140, 15);
  font-size: 32px;
}
.Input{
  display: flex;
  padding-left: 5%;
  height: 40px;
  width: 45%;
  margin-top: 25px;
  background-color: #101010;
  border: 1px solid rgb(230, 140, 15);
  border-radius: 18px;
  color: rgb(230, 140, 15);;
  font-size: 24px; 
}
.SubmitBtn{
  height: 50px;
  width: 25%;
  margin-top: 30px;
  margin-bottom: 30px;
  border-radius: 18px;
  background-color: rgb(230, 140, 15);
  color: black;
  font-size: 22px;
}

.Form2{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  width: 55%;
  background-color: #101010;
  border-radius: 10px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.FormRow{
  display: flex;
  flex-direction: row;
  height: 40px;
  width: 90%;
  margin-top: 25px;
  align-items: center;
}
.Input2{
  
  /* padding-left: 5%; */
  height: 40px;
  width: 46%;
  text-align: center;
  margin-right:2%;
  margin-left: 2%;
  background-color: #101010;
  border: 1px solid rgb(230, 140, 15);
  border-radius: 18px;
  color: rgb(230, 140, 15);;
  font-size: 24px; 
}
.Footer{
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}
.FooterR1{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}
#footerlogo{
  height: 220px;
  width: 220px;
}
.FooterDiv{
  height: 220px;
  width: 35%;
  display: flex;
  flex-direction: column;
  text-align: start;
  padding-left: 20px;
}
.HeadingFooter{
  color: rgb(230, 140, 15);
  font-size: 22px;
}
.FooterDivRow{
  display: flex;
  flex-direction: row;
  height: 60px;
  width: 100%;
}
.head{
  height: 100%;
  width: 25%;
  color: rgb(230, 140, 15);
  font-size: 16px;
  font-style: bold;
  margin: 0px;
  
}
.body{
  height: 100%;
  width: 75%;
  color: white;
  font-size: 16px;
  margin: 0px;
}
.FooterDivRow1{
  display: flex;
  flex-direction: row;
  height: 50px;
  width: 100%;
}
.head1{
  height: 100%;
  width: 25%;
  color: rgb(230, 140, 15);
  font-size: 16px;
  font-style: bold;
  margin: 0px;
  display: flex;
  align-items: center;
  
}
.body1{
  height: 100%;
  width: 75%;
  color: white;
  font-size: 16px;
  margin: 0px;
  display: flex;
  align-items: center;
  
}
.FooterR2{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}
.FooterText{
  font-size: 18px;
  color: white;
}

.DashboardHeading{
  height: 70px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top:10px;
  margin-bottom: 10px;
}
.DashHead{
  color: rgb(230, 140, 15);
}
.TabsDiv{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 90%;
  width: 95%;
  background-color: #101010;
  border-radius: 10px;
  padding-top: 40px;
  padding-bottom: 40px;
}
.Tabs{
  display: flex;
  flex-direction: row;
  height: 70px;
  width: 100%;
  background-color: #101010;
}
.TabButtons{
  height: 60px;
  width: 10%;
  margin-left: 1%;
  background-color: #101010;
  color: white;
  border: none;
  border-bottom: 3px solid rgb(230, 140, 15);
  font-size: 22px;
  cursor: pointer;
  transition: 0.3s;
}
.TabButtons:hover{
  background-color: #242424;
}
.TabButtons.active{
  background-color: black;
}

#Profile{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 600px;
  width: 98%;
  margin: 1px;
  border-radius: 10px;
  background-color:black;
}
#Courses{
  display: none;
  flex-direction: column;
  align-items: center;
  height: 600px;
  width: 98%;
  margin: 1px;
  border-radius: 10px;
  background-color:black;
}
#Exam{
  display: none;
  flex-direction: column;
  align-items: center;
  height: 600px;
  width: 98%;
  margin: 1px;
  border-radius: 10px;
  background-color:black;
}
#Results{
  display: none;
  flex-direction: column;
  align-items: center;
  height: 600px;
  width: 98%;
  margin: 1px;
  border-radius: 10px;
  background-color:black;
}
.profileheader{
  display: flex;
  flex-direction: row;
  height: 40%;
  width: 100%;
  align-items: center;
}
.profilehead{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 75%;
}
.profileimg{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 25%;
  justify-content: center;
  align-items: center;
}
#profilepic{
  height: 75%;
  width: 55%;
}

.TableDiv{
  display: flex;
  height: auto;
  width: 85%;
  background-color: black;
  margin: 1%;
}
.tableButton{
  height: 40px;
  width: 130px;
  border: none;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(31, 109, 19);
  color: white;
  font-size: 14px;
}
.tdBtn{
  display: flex;
  justify-content: center;
}
.QuestionCard{
  height: auto;
  width: 98%;
  margin: 1%;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  text-align: start;
  color: white
}
.RadioGroup{
  display: flex;
  flex-direction: column;
}
.RadioBtn{
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 50px;
}
.inputcheck{
  height: 30px;
  width: 20px;
  margin-right: 10px;
}
.checklabel{
  font-size: 25px;
}
.ButtonDiv{
  display: flex;
  flex-direction: row;
  height: 50px;
  width: 100%;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
}
.btn2{
  height: 40px;
  width: 20%;
  background-color: green;
  margin-left: 5%;
  margin-right: 5%;
  color: white;
  border: none;
  border-radius: 6px;
}
.btn3{
  height: 40px;
  width: 20%;
  background-color: rgb(157, 38, 38);
  margin-left: 5%;
  margin-right: 5%;
  color: white;
  border: none;
  border-radius: 6px;
}

.timertext {
    font-size: 24px;
    font-weight: bold;
    color: rgb(230, 140, 15);
    margin-bottom: 10px;
}
/* Exam Instructions Styles */

.InstructionsContainer {
  max-width: 800px;
  background-color: #101010;
  border-radius: 8px;
}

.InstructionsTitle {
  color: rgb(230, 140, 15);
  font-size: 24px;
  font-weight: bold;
  
}

.InstructionsText {
  color: rgb(230, 140, 15);
  font-size: 16px;
  line-height: 1.5;
}

.InstructionsList {
text-align: justify;
  font-size: 16px;
  line-height: 1.5;
  color: rgb(230, 140, 15);
}


.InstructionsFooter {
  font-size: 14px;
  color: #101010;
}

/* Apply the styles to the container element */
.ExamInstructions {
  display: flex;
  background-color: #101010;
}

